import WorldMap from '../core/WorldMap';
import Entity from '../core/Entity';

export function createTestWorld(): WorldMap {
    const worldMap = new WorldMap({
        width: 10,
        height: 10,
        emptyTileData: {
            terrain: {
                title: '',
                color: '#fafffa',
            },
            feature: null,
        },
    });

    for (let y = 0; y < 10; y++) {
        worldMap.setTileData(6, y, {
            terrain: {
                title: '',
                color: '#fafbff',
            },
            feature: null,
        });
    }

    worldMap.getTile(6, 3).data.feature = {
        title: '',
        icon: '',
        description: '  ,   ',
    };

    const trees = [
        { x: 2, y: 2 },
        { x: 4, y: 2 },
        { x: 8, y: 6 },
        { x: 9, y: 7 },
        { x: 9, y: 8 },
    ];

    for (let tree of trees) {
        worldMap.setTileData(tree.x, tree.y, {
            terrain: {
                title: '',
                color: '#fafffa',
            },
            feature: {
                title: '',
                icon: '',
                description: ' ',
            },
        });
    }

    worldMap.addEntity(3, 5, new Entity({
        id: 'player',
        icon: '',
        title: '',
        initialItems: [
            {
                id: 'apple',
                icon: '',
                title: '',
                description: '     ',
                amount: 2,
            },
            {
                id: 'bottleWithWater',
                icon: '',
                title: '  ',
                description: '    ',
                amount: 1,
            },
            {
                id: 'axe',
                icon: '',
                title: '',
                description: ' ,  - ',
                amount: 1,
            },
        ],
    }));

    return worldMap;
}
